@media screen and (max-width: 1023px){
	.container{
		max-width: 960px;
	}
	.container-small {
		max-width: 696px;
	}
	.main {
		background-image: none;
	}
	.host-events {
		justify-content: space-around;
		&__img {
			width: 200px;
			height: 200px;
		}
		&__name {
			font-size: 20px;
			line-height: 28px;
		}
	}
	.fancybox__item {
		padding: 0 10px;
	}
	.video-gallery__img {
		width: 444px;
	}
}


@media screen and (max-width: 959px){
	.container{
		max-width: 768px;
	}
	.header-content {
		font-size: 16px;
		line-height: 20px;
		&__span {
			font-size: 28px;
    		line-height: 36px;
		}	
	}
	.main-inner {
		bottom: 0;
		&__h1 {
			font-size: 23px;
			line-height: 31px;
		}
	}
	.main-spruces_left, .main-spruces_right {
		top: 110px;
	}
	.subtitle {
		font-size: 22px;
		line-height: 30px;
		&::before {
			width: 80px;
    		height: 3px;
		}
	}
	.main-text {
		&__paragraph {
			font-size: 16px;
			line-height: 24px;
		}
	}
	.accent {
		font-size: 16px;
		line-height: 24px;
	}
	.host-events__img {
		width: 170px;
		height: 170px;
	}
	.video {
		&__link {
			&::before {
				width: 60px;
				height: 60px;
			}
			&::after {
				width: 100px;
				height: 100px;
			}
		}
	}
	.video-gallery__img {
		width: 354px;
	}
	.form {
		padding: 30px 40px;
		&__paragraph {
			font-size: 16px;
			line-height: 20px;
		}
	}
	.contacts__title {
		font-size: 28px;
		line-height: 36px;
	}
}
.social {
	&__icon {
		width: 40px;
		height: 40px;
	}
}


@media screen and (max-width: 769px){
	.container{
		max-width: 600px;
	}
	.header__logo {
		display: block;
		margin: 40px 0 40px auto;
	}
	.header-inner {
		padding: 16px 0;
	}
	.header-content {
		&__span {
			font-size: 26px;
			line-height: 30px;
		}
		&__img {
			display: none;
		}
		&__text_right {
			text-align: left;
		}
	}
	.main {
		padding-bottom: 40px;
	}
	.main-section {
		margin-top: 70px;
	}
	.main-photo {
		filter: none;
	}
	.social {
		margin-bottom: 40px;
	}
	.main-inner {
		position: static;
		transform: translateY(-3px);
		&__title {
			padding: 16px 0;
		}
		&__h1 {
			font-size: 18px;
    		line-height: 26px;
		}
		&__btn {
			width: 196px;
			height: 38px;
			font-size: 16px;
			margin-bottom: 0;
		}
	}
	.main-spruces_left, .main-spruces_right {
		display: none;
	}
	.subtitle {
		font-size: 19px;
		&::before {
			width: 0;
			height: 0;
			margin: 0;
		}
	}
	.accent {
		&::before {
			height: 2px;
			margin-bottom: 16px;
		}
		&::after {
			height: 2px;
			margin-top: 16px;
		}
	}
	.host-events {
		&__img {
			width: 150px;
			height: 150px;
		}
		&__name {
			font-size: 16px;
			line-height: 16px;
		}
	}
	.video {
		filter: none;
		margin-top: 40px;
	}
	.fancybox__item {
		filter: none;	
	}
	.video-gallery {
		&__link {
			display: block;
			margin: 0 auto 0 auto;
			filter: none;
			&:last-child {
				margin-top: 20px;
			}
		}
		&__img {
			width: 100%;
		}
	}
	.form {
		box-shadow: none;
		background: none;
		padding: 0;
	}
	.contacts {
		padding: 10px 0;
		&__title {
			font-size: 20px;
			line-height: 28px;
		}
		&__item:not(:last-child) {
			padding: 0;
		}
		&__list {
			flex-direction: column;
			align-items: center;
			font-size: 16px;
			line-height: 22px;
		}
		&__line {
			display: none;
		}
		&__phone {
			background: none;
			padding: 0;
		}
		&__email {
			background: none;
			padding: 0;
		}
	}
}
// Стили для страницы "Политика конфиденциальности"
.main__title {
	font-size: 26px;
	line-height: 34px;
	margin: 55px 0 0;
}
.politika-kd__paragraph {
	font-size: 16px;
	line-height: 24px;
}
.main-text__paragraph, .main__list {
	font-size: 16px;
	line-height: 24px;
}


@media screen and (max-width: 599px){
	.container{
		max-width: 480px;
	}
	.header__logo {
		margin: 30px auto 30px auto;
	}
	.header-inner {
		padding: 10px 0;
	}
	.header-content {
		flex-direction: column;
		justify-content: center;
		&__text {
			text-align: center;
		}
		&__span {
			font-size: 24px;
			line-height: 24px;
		}
		&__img {
			flex-grow: 0;
		}
		&__text_right {
			margin-top: 15px;
			text-align: center;
		}
	}
	.social {
		justify-content: center;
	}
	.main-spruces_left, .main-spruces_right {
		top: 215px;
	}
	.subtitle {
		display: block;
		font-size: 20px;
		line-height: 26px;
		text-align: center;
	}
	.host-events {
		flex-direction: column;
		&__wrapper:not(:first-child) {
			margin-top: 32px;
		}
		&__img {
			width: 170px;
			height: 170px;
			margin: 0 auto;
		}
	}
	.video__link {
		&::before {
			width: 40px;
			height: 40px;
		}
		&::after {
			width: 70px;
			height: 70px;
		}
	}
	.video-gallery__link {
		&::before {
			width: 35px;
			height: 35px;
		}
		&::after {
			width: 60px;
			height: 60px;
		}
	}
	.form {
		margin-top: 30px;
		&__group {
			margin-top: 24px;
		}
		&__paragraph {
			font-size: 14px;
			line-height: 20px;
		}
		&__necessarily {
			font-size: 14px;
			line-height: 18px;
		}
	}
	.footer {
		padding: 20px 0;
	}
	.footer-inner {
		flex-direction: column;
		.social {
			margin: 20px 0 0 0;
		}
		&__logo1 {
			order: 1;
		}
		&__social {
			order: 3;
		}
		&__logo2 {
			order: 2;
		}
	}
}
// Стили для страницы "Политика конфиденциальности"
ol, ul {
	padding-left: 20px;
}
.main__link {
	font-size: 16px;
}

@media screen and (max-width: 479px){
	.container{
		max-width: 100%;
	}
	.header-content {
		&__span {
			font-size: 20px;
			line-height: 20px;
		}
	}
	.main-inner {
		&__h1 {
			font-size: 16px;
		}
		&__title {
			padding: 5px 5px;
		}
	}
	.host-events {
		margin-top: 30px;
		&__name {
			margin-top: 15px;
		}
	}
	.video {
		margin-top: 30px;
	}
	.video-gallery {
		margin-top: 30px;
	}
}
// Стили для страницы "Политика конфиденциальности"
.main__title {
	font-size: 20px;
}

@media screen and (max-width: 397px) {
	.main-inner__h1 br {
		display: none;
	}
}


@media screen and (max-width: 379px){
	.main {
		padding: 30px 0;
	}
	.main-section {
		margin-top: 50px;
	}
	.social {
		margin-bottom: 30px;
		&__icon {
			width: 30px;
			height: 30px;
		}
	}
	.host-events {
		margin-top: 40px;
	}
	.gallery {
		flex-direction: column;
		justify-content: space-between;
	}
	.fancybox {
		&__item:not(:first-child) {
			margin-top: 10px;
		}
		&__img {
			max-width: 185px;
			margin: 0 auto;
		}
	}
	.form {
		&__label {
			font-size: 16px;
		}
		&__input {
			height: 35px;
		}
		&__paragraph {
			font-size: 12px;
			line-height: 16px;
		}
	}
	.contacts__list {
		font-size: 16px;
		margin-top: 10px;
	}
}

@media screen and (max-width: 330px) {
	.main-inner__h1 {
		font-size: 15px;
	}
}